import React from "react";

import './header.css'

import profileImg from '../img/profile.jpg'
import inventoryImg from '../img/inventory.png'

import ButtonConnect from "../button-connect";



const languageListOpen = (e) =>{
    e.target.closest('.language-change').classList.toggle('open');
}


const Header = () => {
    return (
        <header className="header">
            <div className="header-wrapper">
                <div className="header-wrapper__block">
                    <div className="profile header__profile">
                        <a className="profile__img" href="#">
                            <img src={profileImg} alt='profile'/>
                        </a>
                    </div>
                    <div onClick={languageListOpen} className="language-change">
                        <div className="language-change__current">
                            <svg className="icon icon-uk-flag ">
                                <g clipPath="url(#cclip0_97_773)">
                                    <path d="M16 2.666H0v10.667h16V2.666z" fill="#F0F0F0"/>
                                    <path d="M9 2.666H7v4.333H0v2h7v4.334h2V8.999h7V7H9V2.666z" fill="#D80027"/>
                                    <path
                                        d="M12.306 9.854L16 11.907V9.854h-3.694zm-2.566 0L16 13.333v-.984l-4.49-2.495H9.74zm4.592 3.479L9.74 10.781v2.552h4.593z"
                                        fill="#0052B4"/>
                                    <path d="M9.74 9.854L16 13.333v-.984l-4.49-2.495H9.74z" fill="#F0F0F0"/>
                                    <path d="M9.74 9.854L16 13.333v-.984l-4.49-2.495H9.74z" fill="#D80027"/>
                                    <path d="M2.823 9.854L0 11.423V9.854h2.823zm3.437.443v3.036H.798l5.464-3.036z"
                                          fill="#0052B4"/>
                                    <path d="M4.49 9.854L0 12.35v.984l6.26-3.479H4.49z" fill="#D80027"/>
                                    <path
                                        d="M3.694 6.145L0 4.093v2.052h3.694zm2.566-.001L0 2.666v.984l4.49 2.494h1.77zM1.668 2.666L6.26 5.218V2.666H1.668z"
                                        fill="#0052B4"/>
                                    <path d="M6.26 6.144L0 2.666v.984l4.49 2.494h1.77z" fill="#F0F0F0"/>
                                    <path d="M6.26 6.144L0 2.666v.984l4.49 2.494h1.77z" fill="#D80027"/>
                                    <path
                                        d="M13.177 6.145L16 4.576v1.569h-2.823zM9.74 5.703V2.667h5.463L9.74 5.703z"
                                        fill="#0052B4"/>
                                    <path d="M11.51 6.144L16 3.65v-.984L9.74 6.144h1.77z" fill="#D80027"/>
                                </g>
                                <defs>
                                    <clipPath id="cclip0_97_773">
                                        <path fill="#fff" d="M0 0h16v16H0z"/>
                                    </clipPath>
                                </defs>
                            </svg>
                        </div>
                        <div className="language-change__list language-list">
                            <div className="language-list__item language-item">
                                <a className="language-link"
                                   href="ru.html">
                                    <svg className="icon icon-ru-flag ">
                                        <path d="M0 3.333v13.333h20V3.333H0z" fill="#F0F0F0"/>
                                        <path d="M20 3.333H0v13.333h20V3.333z" fill="#0052B4"/>
                                        <path d="M20 3.333H0v4.444h20V3.333z" fill="#F0F0F0"/>
                                        <path d="M20 12.222H0v4.444h20v-4.444z" fill="#D80027"/>
                                    </svg>
                                </a>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="header-wrapper__block header-wrapper__block_connect">
                    <div className="connect-value-box">
                        <ButtonConnect/>
                    </div>
                </div>
                <div className="header-wrapper__block header-wrapper__block_inventory">
                    <div className="social-icon header-social__icon">
                        <svg className="icon icon-telegram" viewBox="0 0 42 42" id="telegram">
                            <path d="M21 .656C9.761.656.656 9.762.656 21 .656 32.238 9.762 41.344 21 41.344c11.239 0 20.344-9.106 20.344-20.344C41.344 9.762 32.238.656 21 .656zm9.992 13.938l-3.339 15.734c-.247 1.116-.91 1.386-1.837.86L20.73 27.44l-2.453 2.363c-.27.27-.5.5-1.026.5l.361-5.177 9.426-8.515c.41-.36-.091-.566-.633-.205L14.756 23.74l-5.02-1.567c-1.09-.344-1.116-1.09.23-1.616l19.614-7.564c.91-.328 1.706.222 1.41 1.6l.002.002z"
                                  fill="#4A87C8"/>
                            <path d="M30.992 14.594l-3.339 15.734c-.247 1.116-.91 1.386-1.837.861l-5.086-3.748-2.453 2.362c-.27.27-.5.5-1.026.5l.361-5.176 9.426-8.516c.41-.36-.091-.565-.633-.204l-11.649 7.332-5.02-1.567c-1.09-.344-1.116-1.09.23-1.615l19.614-7.564c.91-.328 1.706.221 1.41 1.6h.002z"
                                  fill="#fff"/>
                        </svg>
                    </div>
                    <div className="inventory-wrapper">
                        <div className="inventory-wrapper__icon">
                            <img src={inventoryImg} />
                        </div>
                        <a className="btn btn-inventory inventory-wrapper__btn" href="#">Inventory</a>
                    </div>
                </div>
            </div>
        </header>
    )
}

export default Header