import React from "react";
import Web3 from "web3";


import './product-grid-item.css'

async function payMeta() {
    await window.ethereum.enable();
    window.web3 = new Web3(window.ethereum);
    await window.web3.eth.getAccounts((error, accounts) => {
        try {
            const params = {
                from: accounts[0],
                to: accounts[0],
                value: 2,
                gas: 39000
            };
            const sendHash = window.web3.eth.sendTransaction(params);
            console.log('txnHash is ' + sendHash);
        } catch (e) {
            console.log("payment fail!");
            console.log(e);
        }
    });

}


const ProductGridItem = ({label, desc, minCount, maxCount, price,img, hiddenPrice}) => {

    return (
        <div className="product-grid__item product-item">
            <div className="product-item__img">
                <img src={img}/>
            </div>
            <p className="product-item__title">{label}</p>
            <p className="product-item__desc">{desc}</p>
            <p className="product-item__price">{price} BUSD</p>
            <span className="product-item__count">{minCount}/{maxCount}</span>
            <button className="product-item__btn" onClick={payMeta}>Buy</button>
        </div>
    )
}
export default ProductGridItem